@import './libraries/wp-amjl-iconfont.css';

.components-modal__content {
	padding-top: 0 !important;
  padding-bottom: 0 !important;
}

/* Versteckt das Eingabefeld f√ºr die RangeControl im Modal */
.amjl-icon-modal .components-range-control__number {
	display: none !important;
}

/* Standard (Desktop): show sidebar, hide mobile panel */
.amjl-icon-modal .amjl-filter-panel--mobile {
	display: none;
}

/* Mobile: show collapsible filter panel below tabs */
@media (max-width: 782px) {
	/*
    .amjl-icon-modal .amjl-sidebar {
		display: none !important;
	}
    */

	.amjl-icon-modal .amjl-filter-panel--mobile {
		display: block;
		padding: 12px;
		border-top: 1px solid #eee;
		background: #fff;
		position: sticky;
		top: 44px; /* height of tabs */
		z-index: 15;
	}
}
